// API client for interacting with the backend
import { TaskCreate, TaskUpdate, TaskPatch, User } from '@/types'; // Adjust path as needed

const API_BASE_URL = process.env.NEXT_PUBLIC_API_BASE_URL || 'http://localhost:8000';

/**
 * Generic API request function
 * @param endpoint API endpoint path
 * @param options Request options
 * @returns Promise with response data
 */
async function apiRequest<T>(
  endpoint: string,
  options: RequestInit = {}
): Promise<T> {
  const url = `${API_BASE_URL}${endpoint}`;

  const config: RequestInit = {
    headers: {
      'Content-Type': 'application/json',
      ...(options.headers || {}),
    },
    ...options,
  };

  // Add auth token if available
  if (typeof window !== 'undefined') {
    const token = localStorage.getItem('auth_token');
    if (token) {
      config.headers = {
        ...config.headers,
        Authorization: `Bearer ${token}`,
      };
    }
  }

  const response = await fetch(url, config);

  if (!response.ok) {
    const errorData = await response.text();
    throw new Error(errorData || `HTTP error! status: ${response.status}`);
  }

  // Handle responses that don't have JSON bodies (like DELETE)
  if (response.status === 204 || response.headers.get('content-length') === '0') {
    return {} as T;
  }

  return response.json();
}

/**
 * AUTHENTICATION API
 */

/**
 * Register a new user
 * @param userData User registration data
 * @returns Promise with created user data
 */
export async function registerUser(userData: {
  email: string;
  name: string;
  password: string;
}): Promise<User> {
  return apiRequest<User>('/api/auth/register', {
    method: 'POST',
    body: JSON.stringify(userData),
  });
}

/**
 * Login user
 * @param credentials User login credentials
 * @returns Promise with login response containing token
 */
export async function loginUser(credentials: {
  email: string;
  password: string;
}): Promise<{ access_token: string; token_type: string; user: User }> {
  return apiRequest('/api/auth/login', {
    method: 'POST',
    body: JSON.stringify(credentials),
  });
}

/**
 * Logout user
 * @returns Promise indicating success
 */
export async function logoutUser(): Promise<void> {
  await apiRequest('/api/auth/logout', {
    method: 'POST',
  });
}

/**
 * TASKS API
 */

/**
 * Get all tasks for a user
 * @param userId User ID
 * @param params Query parameters (status, sort)
 * @returns Promise with array of tasks
 */
export async function getUserTasks(
  userId: string,
  params?: { status?: 'all' | 'pending' | 'completed'; sort?: 'created' | 'title' | 'due_date' }
): Promise<{ tasks: any[]; count: number }> {
  const queryParams = new URLSearchParams(params as any).toString();
  const queryString = queryParams ? `?${queryParams}` : '';
  return apiRequest(`/api/${userId}/tasks${queryString}`, {
    method: 'GET',
  });
}

/**
 * Create a new task for a user
 * @param userId User ID
 * @param taskData Task creation data
 * @returns Promise with created task
 */
export async function createTask(userId: string, taskData: TaskCreate): Promise<any> {
  return apiRequest(`/api/${userId}/tasks`, {
    method: 'POST',
    body: JSON.stringify(taskData),
  });
}

/**
 * Get a specific task
 * @param userId User ID
 * @param taskId Task ID
 * @returns Promise with task data
 */
export async function getTask(userId: string, taskId: number): Promise<any> {
  return apiRequest(`/api/${userId}/tasks/${taskId}`, {
    method: 'GET',
  });
}

/**
 * Update a specific task
 * @param userId User ID
 * @param taskId Task ID
 * @param taskData Task update data
 * @returns Promise with updated task
 */
export async function updateTask(userId: string, taskId: number, taskData: TaskUpdate): Promise<any> {
  return apiRequest(`/api/${userId}/tasks/${taskId}`, {
    method: 'PUT',
    body: JSON.stringify(taskData),
  });
}

/**
 * Delete a specific task
 * @param userId User ID
 * @param taskId Task ID
 * @returns Promise indicating success
 */
export async function deleteTask(userId: string, taskId: number): Promise<{ message: string; deleted_task_id: number }> {
  return apiRequest(`/api/${userId}/tasks/${taskId}`, {
    method: 'DELETE',
  });
}

/**
 * Toggle task completion status
 * @param userId User ID
 * @param taskId Task ID
 * @param completionData Task completion data
 * @returns Promise with updated task
 */
export async function toggleTaskCompletion(
  userId: string,
  taskId: number,
  completionData: TaskPatch
): Promise<any> {
  return apiRequest(`/api/${userId}/tasks/${taskId}/complete`, {
    method: 'PATCH',
    body: JSON.stringify(completionData),
  });
}

/**
 * GENERIC API UTILITIES
 */

/**
 * Check if user is authenticated by making a test request
 * @returns Promise with authentication status
 */
export async function checkAuth(): Promise<boolean> {
  try {
    // This would be a protected endpoint that verifies the token
    // For now we'll just return true if we have a token
    if (typeof window !== 'undefined') {
      const token = localStorage.getItem('auth_token');
      return !!token;
    }
    return false;
  } catch (error) {
    console.error('Auth check failed:', error);
    return false;
  }
}